.list-group{
	color: #999;font-size: 16px;border-bottom: 1px solid #eee;background: #fff;margin-bottom: 0;
	p.title{
		padding:15px;
	}
	.badge{
		background: #fff;color: #454545;font-weight: bold;font-size: 16px;
		&.icon{
			background: url(../images/Map-Locator.png);background-size: 20px 20px;width: 20px;height: 20px;display: block;
		}
	}
	.list-group-item{
		border:0;
	}
}
.media{
	background: #fff;margin:0;padding:15px;
	.media-object{
		width: 120px;
	}
	p.title{
		padding:15px;color: #999;font-size: 16px;padding-left: 0;
	}
	h4{
		color: #999;font-size: 14px;
		.icon{
			width:20px;height: 20px;display: block;float: right;margin-right: 15px;background: url(../images/arrow_right_gray.png);background-size: 20px 20px;
		}
	}
	h1{
		font-size: 18px;
	}
}
.button{
	button{
		display: block;width: 96%;margin:10px auto;background: #2AAE66;height: 40px;color: #fff;border:0;border-radius: 20px;outline: none;
	}
}

body{
	background: #f2f2f2;
}